import React, {lazy, Suspense} from 'react'
import LayoutContent from "../../../components/layout-components/LayoutContent";
import Loading from "../../../components/shared-components/Loading";
import {Redirect, Route, Switch} from "react-router-dom";

const BookingView = ({match})=> {
    return (
        <LayoutContent style={{marginTop: 120, minHeight: "calc(100vh)"}}>
            <Suspense fallback={<Loading cover="content"/>}>
                <Switch>
                    <Route path={`${ match.url }/now/:id`} component={lazy(()=> import("./booking-rooms/booking-rooms-now/index"))}/>
                    <Route path={`${ match.url }/`} exact component={lazy(()=> import("./booking-rooms/index"))}/>
                    <Route path={`${ match.url }/:id`} component={lazy(()=> import("./booking-rooms/booking-rooms-detail"))}/>
                    {/*<Redirect to={`${ match.url }/room`} from={match.url}/>*/}
                </Switch>
            </Suspense>
        </LayoutContent>
    )
}

BookingView.propTypes = {}
BookingView.defaultProps = {}

export default BookingView